
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
data () {
return {
list: []
}
},
methods: {
toCharts () {
this.$router.push({
path: '/hccCharts'
})
},
_hccActivityFansInfo () {
this.$tupHttp(this, 'manage-app/hccActivity/hccActivityFansShareInfo', 'POST').then(res => {
this.list = res.data.models
this.$nextTick(() => {
for (let i = 0; i < this.list.length; i++) {
new Aliplayer({
id: 'player' + i,
source: this.list[i].videoUrl,
cover: this.list[i].coverUrl,
width: '100%',
height: '150px',
autoplay: false,
isLive: false,
rePlay: false,
playsinline: true,
preload: true,
controlBarVisibility: 'hover',
useH5Prism: true
}, function (player) {

})
}
})
})
}
},
created () {
this._hccActivityFansInfo()
},
components: {
XHeader
}
}



